<!-- with.vue -->
<template>
	<div class="ui-recommend-wrapper" v-if="list.length > 0">
		<div class="wrapper-swipe">
			<mt-swipe
				v-bind:style="getWrapperStyle"
				:auto="0"
				:show-indicators="false"
				@change="handleChange"
				:default-index="currentIndex"
				:stop-propagation="true"
				:speed="100"
			>
				<mt-swipe-item v-for="(item, index) in list" :key="index">
					<div class="image-swipe-wrapper">
						<div
							v-for="(image, j) in item[0]"
							@click="goDetail(image.id)"
							v-bind:style="getItemStyle"
						>
							<img
								:src="image.photos[0].thumb"
								v-if="image.photos.length > 0"
							/>
							<img
								src="../../../assets/image/change-icon/default_image_02@2x.png"
								v-if="image.photos.length <= 0"
							/>
							<span>{{ image.current_price }}</span>
						</div>
					</div>
				</mt-swipe-item>
			</mt-swipe>
		</div>

		<div class="swiper-indicators" v-if="list.length > 1">
			<div class="ui-indicator">
				<div
					class="indicator-item"
					v-for="(item, index) in indicatorArray"
					v-bind:class="{ active: item.index == currentIndex }"
				></div>
			</div>
		</div>

		<div class="ui-recommend-all" v-on:click="goRecommend()"></div>
	</div>
</template>

<script>
import { productAccessoryList } from '../../../api/product'
import { mapState, mapMutations } from 'vuex'
export default {
	data() {
		return {
			list: [],
			indicatorArray: [],
			currentIndex: 0
		}
	},

	created() {
		this.getRecommendList()
	},

	computed: {
		...mapState({
			currentProductId: state => state.detail.currentProductId
		}),

		getWrapperStyle: function() {
			const { width, height } = window.screen
			let itemWidth = width - 30
			let itemHeight = (width - 30 - 16) / 3
			return {
				width: itemWidth + 'px',
				height: itemHeight + 'px'
			}
		},
		getItemStyle: function() {
			const { width, height } = window.screen
			let itemWidth = (width - 30 - 16) / 3
			let itemHeight = itemWidth
			return {
				width: itemWidth + 'px',
				height: itemHeight + 'px'
			}
		}
		// CODE REVIEW: 
	},

	methods: {
		/*
				getRecommendList: 
			*/
		getRecommendList() {
			let params = {
				product: this.currentProductId ? this.currentProductId : '',
				page: 1,
				per_page: 10
			}
			productAccessoryList(params.product, params.page, params.per_page).then(
				res => {
					if (res) {
						this.list = this.buildList(res.products)
						this.buildSwipeIndicators()
					}
				}
			)
		},

		/*
				buildList
				@params res 
			*/
		buildList(res) {
			let index = Math.ceil(res.length / 3)
			let newArray = []
			if (index) {
				for (let i = 0; i <= index - 1; i++) {
					let subArray = []
					subArray.push(res.slice(i * 3, i * 3 + 3))
					newArray.push(subArray)
				}
			}
			return newArray
		},

		/*
				buildSwipeIndicators: 
			*/
		buildSwipeIndicators() {
			let photos = this.list
			for (let i = 0, len = photos.length - 1; i <= len; i++) {
				photos[i].index = i
				this.indicatorArray.push(photos[i])
			}
		},

		/*
				handleChange: 
				@params: index index
			 */
		handleChange(index) {
			this.currentIndex = index
		},

		/*
				goRecommend: 
			 */
		goRecommend() {
			let params = {}
			params.product = this.currentProductId
			this.$router.push({ name: 'accessory', query: params })
		},

		goDetail(id) {
			let data = Object.assign({}, { id: id })
			this.$router.push({ name: 'product', query: { id: id } })
		}
	}
}
</script>

<style lang="scss" scoped>
.ui-recommend-wrapper {
	background: #ffffff;
	margin-top: 8px;
	margin-bottom: 10px;
	.wrapper-swipe {
		padding: 15px 15px 0px 15px;
		.mint-swipe {
			height: 105px;
			.mint-swipe-items-wrap {
				.mint-swipe-item {
				}
			}
		}
	}

	.swiper-indicators {
		position: relative;
		margin-top: 20px;
		bottom: 0px;
	}

	.ui-recommend-all {
		height: 44px;
		background: #ffffff;
		border-top: 1px solid #e8eaed;
		font-size: 15px;
		font-family: 'PingFangSC-Regular';
		color: rgba(78, 84, 93, 1);
		width: 100%;
		flex-basis: 100%;
		display: flex;
		justify-content: center;
		align-content: center;
		align-items: center;
		margin-top: 20px;
	}

	div.image-swipe-wrapper {
		display: flex;
		justify-content: space-between;
		align-content: center;
		align-items: center;
		div {
			position: relative;
			border: 1px solid #efeff4;
			border-radius: 2px;
			img {
				width: 100%;
				height: 100%;
				padding: 0px;
				margin: 0px;
			}
			span {
				position: absolute;
				bottom: 0px;
				width: 100%;
				display: inline-block;
				height: 24px;
				line-height: 24px;
				font-size: 16px;
				font-family: 'PingFangSC-Regular';
				color: #ffffff;
				background: rgba(0, 0, 0, 0.5);
				border-radius: 0px 0px 2px 2px;
			}
		}
	}
}
</style>



